.derecha{
   
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0rem;

    @include tablet{
        align-items: flex-end;
        

    }

    .dark-mode-boton{
        width: 3rem;
        margin-bottom: 2rem;
        filter:invert(100%);

        &:hover{
            cursor: pointer;

        }



      

     
    }
}






.barra{
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    align-items: center;

    @include tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;


    }
    

}

h1{
    color: $blanco;
}





.navegacion{

    visibility: hidden;
    opacity: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear,height .5s linear ;
    height: 0;
    a:hover{
        color: $verde;

    }



   


    @include tablet{
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;


    }

    a{
        color:$blanco;
        text-decoration:none ;
        font-size: 2.5rem;
        display: block;
        text-align: center;

        @include tablet{
            margin-right: 2rem;

            &:last-of-type{
                margin-right: 0;
            }
        }
    

    }

}





.mobile-menu{

    
    @include tablet{
        display: none;

    }
    img{
        width: 5rem;
     
        

    }

}

.mostrar{
    
     display: block;
     visibility: visible;
     opacity: 1;
     height: auto;

     @include tablet{
        display: flex;
     }

}


